import InfoAboutVideo from './InfoAboutVideo';

const videoPerPage = 15;

export default class AppModel {
  constructor() {
    this.state = {
      url: 'https://www.googleapis.com/youtube/v3/search?key=AIzaSyDZLUQEOHmDNl1eD7hpfI3U0wJMVLV8EuE&type=video&part=snippet&maxResults=%videoPerPage&q=%query',
      urlViews: 'https://www.googleapis.com/youtube/v3/videos?key=AIzaSyDZLUQEOHmDNl1eD7hpfI3U0wJMVLV8EuE&id=',
      settingsUrlViews: '&part=snippet,statistics',
      currentPage: 0,
      nextPageToken: 0,
    };
    this.request = '';
  }

  // eslint-disable-next-line class-methods-use-this
  extractClipInfo(data) {
    // console.log(data);
    const listInfoAboutVideo = [];
    data.items.forEach((el) => {
      listInfoAboutVideo.push(new InfoAboutVideo(
        el.snippet.title,
        el.id.videoId,
        `https://www.youtube.com/watch?v=${el.id.videoId}`,
        el.snippet.thumbnails,
        el.snippet.description,
        el.snippet.channelTitle,
        // eslint-disable-next-line prefer-template
        new Date(el.snippet.publishedAt).getFullYear() + '-' + new Date(el.snippet.publishedAt).getMonth() + '-' + new Date(el.snippet.publishedAt).getDate(),
        0,
      ));
    });
    // console.log(JSON.parse(JSON.stringify(listInfoAboutVideo)));
    return listInfoAboutVideo;
  }

  async getClips(request) {
    this.request = request;
    // destructuring
    const { url, urlViews, settingsUrlViews } = this.state;

    const response = await fetch(url.replace('%videoPerPage', videoPerPage).replace('%query', request));
    const data = await response.json();
    // console.log(data)
    this.state.nextPageToken = data.nextPageToken;
    // console.log(this.state.nextPageToken);
    const listVideo = this.extractClipInfo(data);
    // const urlViews = `https://www.googleapis.com/youtube/v3/videos?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&id=${idVideos}&part=snippet,statistics`;
    const idVideos = [];
    listVideo.forEach((el) => { idVideos.push(el.videoId); });

    const responseUrlViews = await fetch(urlViews + idVideos + settingsUrlViews);
    const dataViews = await responseUrlViews.json();
    // console.log(dataViews);
    dataViews.items.forEach((el, i) => {
      listVideo[i].viewCount = el.statistics.viewCount;
    });
    return listVideo;
    // fetch(url)
    //   .then(res => res.json())
    //   .then(res => console.log(res));
  }

  async loadChunkClips() {
    // eslint-disable-next-line object-curly-newline
    const { url, urlViews, settingsUrlViews, nextPageToken } = this.state;

    const response = await fetch(`${url.replace('%videoPerPage', videoPerPage).replace('%query', this.request)}&pageToken=${nextPageToken}`);
    const data = await response.json();

    const listVideo = this.extractClipInfo(data);
    const idVideos = [];
    listVideo.forEach((el) => { idVideos.push(el.videoId); });

    const responseUrlViews = await fetch(urlViews + idVideos + settingsUrlViews);
    const dataViews = await responseUrlViews.json();
    // console.log(dataViews);
    dataViews.items.forEach((el, i) => {
      listVideo[i].viewCount = el.statistics.viewCount;
    });
    return listVideo;
  }
}
