import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class App {
  constructor() {
    this.model = new AppModel();
    this.view = '';
  }

  async start() {
    AppView.showSearchInput();
    document.querySelector('.button-search')
      .addEventListener('click', this.handlerClickBtnSearch.bind(this));
  }

  async handlerClickBtnSearch() {
    const request = document.querySelector('.input-search').value;
    if (!request) return;

    const data = await this.model.getClips(request);

    this.view = new AppView(data);

    this.view.render();
    this.addScrollBehaviour();
    this.addPageNavigation();
    this.addTouchBehaviour();
  }

  addScrollBehaviour() {
    const slider = document.querySelector('ul');
    let isDown = false;
    let startX;
    let scrollLeft;

    slider.addEventListener('mousedown', (e) => {
      isDown = true;
      slider.classList.add('active');
      // console.log(e.target.nodeName);
      startX = e.pageX;
      // eslint-disable-next-line prefer-destructuring
      scrollLeft = slider.scrollLeft;
      this.checkActiveClipsToUploadNewVideoList();
    });

    slider.addEventListener('mouseleave', () => {
      isDown = false;
      slider.classList.remove('active');
    });

    slider.addEventListener('mouseup', (e) => {
      isDown = false;
      slider.classList.remove('active');
      if (startX > e.pageX) {
        this.view.nextPage();
      }
      if (startX < e.pageX && scrollLeft !== 0) {
        this.view.prevPage();
      }
    });

    slider.addEventListener('mousemove', (e) => {
      if (!isDown) return; // stop the func from running
      e.preventDefault();
      if (startX > e.pageX) {
        slider.scrollLeft = scrollLeft + document.body.clientWidth * 1.035;
      } else if (startX < e.pageX) {
        slider.scrollLeft = scrollLeft - document.body.clientWidth * 1.035;
      }
    });
  }

  checkActiveClipsToUploadNewVideoList() {
    const slider = document.querySelector('ul');
    const { length } = slider.children;
    for (let i = 0; i < length; i += 1) {
      const item = slider.children[i].getBoundingClientRect();
      if ((item.x + item.width) > 0 && (item.x + item.width) < document.body.clientWidth) {
        if (i > length - 10) {
          this.updateVideoList();
          break;
        }
      } else if (item.x > 0 && item.x < document.body.clientWidth) {
        if (i > length - 10) {
          this.updateVideoList();
          break;
        }
      }
    }
  }

  async updateVideoList() {
    const data = await this.model.loadChunkClips();
    this.view.renderNextChunk(data);
  }

  addPageNavigation() {
    const slider = document.querySelector('ul');
    const carousel = document.querySelector('div.carousel');
    carousel.addEventListener('click', (e) => {
      if (e.target.classList.contains('active')) {
        return;
      }
      if (e.target.getAttribute('data-slide') === '1') {
        slider.scrollLeft -= document.body.clientWidth * 1.035;
        this.view.prevPage();
      }
      if (e.target.getAttribute('data-slide') === '2' || e.target.getAttribute('data-slide') === '3') {
        slider.scrollLeft += document.body.clientWidth * 1.035;
        this.view.nextPage();
      }
      this.checkActiveClipsToUploadNewVideoList();
    });
  }

  addTouchBehaviour() {
    const slider = document.querySelector('ul');
    let isDown = false;
    let startX;
    let scrollLeft;

    slider.addEventListener('touchstart', (e) => {
      isDown = true;
      slider.classList.add('active');
      startX = e.touches[0].pageX;
      // eslint-disable-next-line prefer-destructuring
      scrollLeft = slider.scrollLeft;
      this.checkActiveClipsToUploadNewVideoList();
    });

    slider.addEventListener('touchend', (e) => {
      isDown = false;
      slider.classList.remove('active');
      if (startX > e.changedTouches[0].pageX) {
        this.view.nextPage();
      }
      if (startX < e.changedTouches[0].pageX && scrollLeft !== 0) {
        this.view.prevPage();
      }
    });

    slider.addEventListener('touchmove', (e) => {
      if (!isDown) return; // stop the func from running
      e.preventDefault();
      if (startX > e.touches[0].pageX) {
        slider.scrollLeft = scrollLeft + document.body.clientWidth * 1.01;
      } else if (startX < e.touches[0].pageX) {
        slider.scrollLeft = scrollLeft - document.body.clientWidth * 1.01;
      }
    });
  }
}
