export default class InfoAboutVideo {
  constructor(title, videoId, url, thumbnails, description, author, uploadDate, viewCount) {
    this.title = title;
    this.videoId = videoId;
    this.url = url;
    this.thumbnails = thumbnails;
    this.description = description;
    this.author = author;
    this.uploadDate = uploadDate;
    this.viewCount = viewCount;
  }
}
