import './reset.css';
import './app-view.css';


export default class AppView {
  constructor(listVideo) {
    this.listVideo = listVideo;
    this.page = 1;
  }

  static showSearchInput() {
    const container = document.createElement('div');
    container.className = 'container';

    const inputSearch = document.createElement('input');
    inputSearch.type = 'text';
    inputSearch.className = 'input-search';

    const buttonSearch = document.createElement('button');
    buttonSearch.className = 'button-search';
    buttonSearch.innerHTML = 'Search';

    container.appendChild(inputSearch);
    container.appendChild(buttonSearch);
    document.body.appendChild(container);
  }

  render() {
    // console.log(this.listVideo);
    let content = document.querySelector('ul');
    if (content) {
      content.parentNode.removeChild(content);
    }
    content = document.createElement('ul');
    content.innerHTML = this.listVideo.map((video, i) => `<li data-id=${i}><a href=${video.url}>${video.title}</a>
       <p>${video.author}</p>
       <p>${video.uploadDate}</p>
       <p>${video.viewCount}</p>
       <p>${video.description}</p>
       </li>`).join('');

    // eslint-disable-next-line no-param-reassign, eslint-disable-next-line array-callback-return
    Array.from(content.children).forEach((li, i) => {
      // eslint-disable-next-line no-param-reassign
      li.firstChild.style.background = `url(${this.listVideo[i].thumbnails.medium.url}) no-repeat`;
      // console.log(li);
      li.children[1].classList.add('clip-channel');
      li.children[2].classList.add('clip-date');
      li.children[3].classList.add('clip-views');
    });
    document.body.appendChild(content);
    this.renderCarousel();
  }

  // eslint-disable-next-line class-methods-use-this
  renderCarousel() {
    const bullet = document.createElement('span');
    bullet.className = 'dot';
    bullet.classList.add('active');
    bullet.setAttribute('data-slide', '1');
    bullet.innerHTML = '1';
    const bullet2 = document.createElement('span');
    bullet2.className = 'dot';
    bullet2.setAttribute('data-slide', '2');
    const bullet3 = document.createElement('span');
    bullet3.className = 'dot';
    bullet3.setAttribute('data-slide', '3');
    const div = document.createElement('div');
    div.className = 'carousel';
    div.appendChild(bullet);
    div.appendChild(bullet2);
    div.appendChild(bullet3);
    document.body.appendChild(div);
  }

  renderNextChunk(data) {
    const amountVideoPrev = this.listVideo.length;
    this.listVideo.push(...data);
    const content = document.querySelector('ul');
    content.innerHTML += data.map((video, i) => `<li data-id=${i + amountVideoPrev}><a href=${video.url}>${video.title}</a>
    <p>${video.author}</p>
    <p>${video.uploadDate}</p>
    <p>${video.viewCount}</p>
    <p>${video.description}</p>
    </li>`).join('');

    // eslint-disable-next-line no-param-reassign, eslint-disable-next-line array-callback-return
    Array.from(content.children).forEach((li, i) => {
      if (i >= amountVideoPrev) {
        // eslint-disable-next-line no-param-reassign
        li.firstChild.style.background = `url(${this.listVideo[i].thumbnails.medium.url}) no-repeat`;
      }
    });
  }

  nextPage() {
    const bullet = document.querySelector('span.active');

    if (this.page === 1) {
      this.page += 1;
      bullet.classList.remove('active');
      bullet.innerHTML = '';
      bullet.nextElementSibling.classList.add('active');
      bullet.nextElementSibling.innerHTML = this.page.toString();
    } else {
      this.page += 1;
      bullet.innerHTML = this.page.toString();
    }
  }

  prevPage() {
    this.page -= 1;
    if (this.page < 1) {
      this.page = 1;
      return;
    }
    const bullet = document.querySelector('span.active');
    if (this.page === 1) {
      bullet.classList.remove('active');
      bullet.innerHTML = '';
      bullet.previousElementSibling.classList.add('active');
      bullet.previousElementSibling.innerHTML = this.page.toString();
    } else {
      bullet.innerHTML = this.page.toString();
    }
  }
}
